@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

html { box-sizing: border-box; }

img { max-width: 100%; }

body {
	-moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
	-webkit-text-size-adjust: none;
	font-family: 'Quicksand', sans-serif;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.4;
}

h1 { font-weight: 700; }
h2 { font-weight: 700; }
h3 { font-weight: 700; }
h4 { font-weight: 600; }
h5 { font-weight: 400; }
h6 { font-weight: 400; }

h1,h2,h3,h4,h5,h6,p { margin-bottom: 5px; }

:root {
	--bg-color: #f2f2f7;
	--text-color: #121212;
	--card-color: white;
	--primary: #00cc00;
	--grey-100: #f2f2f7;
	--grey-200: #e5e5ea;
	--grey-300: #d1d1d6;
	--grey-400: #c7c7cc;
	--grey-500: #aeaeb2;
	--grey-600: #8e8e93;
}

@media (prefers-color-scheme: dark) {
  :root {
		--bg-color: #121212;
		--text-color: #f5f6f7;
		--card-color: #1c1c1e;
		--primary: #00ff00;
		--grey-100: #1c1c1e;
		--grey-200: #2c2c2e;
		--grey-300: #3a3a3c;
		--grey-400: #48484a;
		--grey-500: #636366;
		--grey-600: #8e8e93;
	}
