body{background:var(--bg-color);color:var(--text-color);padding:0 24px}body,#root,main{min-height:100vh}header{position:fixed;top:0;right:0;left:0;margin:0 auto;text-align:center;padding:32px 40px}header h1{margin:0}main{display:flex;flex-wrap:wrap;justify-content:center;align-items:center}article{text-align:center;max-width:640px;background:var(--card-color);padding:24px;margin-top:64px;border-radius:9px;display:inline-flex;flex-direction:column}article p{margin-bottom:24px}article p a{color:var(--text-color);text-underline-offset:2px}article p a:hover{color:var(--primary)}article .logos{width:100%;display:flex;justify-content:space-evenly}article .logos a{width:64px}article .logos a svg{width:100%;height:100%;padding:16px;fill:var(--text-color);transition:all .2s ease}article .logos a svg .gradient1{fill:url(#gradient1)}article .logos a svg .gradient2{fill:url(#gradient2)}article .logos a svg .dot{fill:url(#dot)}article .logos a svg stop{stop-color:var(--text-color);transition:all .2s ease}article .logos a.github:hover svg{fill:#c9510c}article .logos a.linkedin:hover svg{fill:#2867b2}article .logos a.facebook:hover svg{fill:#1877f2}article .logos a.instagram:hover svg stop{stop-color:white}article .logos a.instagram:hover svg .gradient stop:nth-child(1){stop-color:#FED576}article .logos a.instagram:hover svg stop:nth-child(2){stop-color:#F47133}article .logos a.instagram:hover svg stop:nth-child(3){stop-color:#BC3081}article .logos a.instagram:hover svg stop:nth-child(4){stop-color:#4C63D2}article .logos a.instagram:hover svg stop.dot-stop{stop-color:#654C9F}.grid .row{display:flex;flex-wrap:wrap}.grid .col{padding:20px}.grid .col{width:33.333%}.insta-gradient{fill:linear-gradient(225deg, #1400c8, #b900b4, #f50000);-webkit-animation:insta 1s linear infinite;animation:insta 1s linear infinite}@-webkit-keyframes insta{to{fill:linear-gradient(-225deg, #1400c8, #b900b4, #f50000)}}@keyframes insta{to{fill:linear-gradient(-225deg, #1400c8, #b900b4, #f50000)}}

